
 *, *::before, *::after {
  box-sizing: border-box;
  /* font-family: Gotham Rounded; */
}

body {
  
  padding: 0;
  margin: 10;
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-image: url("./components/Images/three.gif"), url("./components/Images/travel.gif");
  background-color: white;
  background-repeat: no-repeat;
  background-size: 25% 20%;
  background-position: bottom 10px right 10px, top 10px left;
}
.container {
  width: 900px;
  height: 550px;
  max-width: 80%;
  background-color:#febb02 ;
  border-radius: 5px;
  padding: 30px;
  box-shadow: 0 0 10px 2px;
  font-size: 50px;
}
  
  #size {
    font-size: 25px;
    margin-bottom: 2%;
    color:black;
}
  .question{
    background-color:#3254F4;
    color: white;
    display: inline;
}
  
.btn-grid {
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 10px;
  margin: 70px 0;
}

.btn {
  --hue: var(--hue-neutral);
  border: 1px solid hsl(var(--hue), 100%, 30%);
  background-color: hsl(var(--hue), 100%, 50%);
  border-radius: 5px;
  font-weight: 549;
  padding: 5px 10px;
  color: black;
  font-size: 30px;
  outline: none;
  font-variant-caps:normal;
}


.btn:hover {
  border-color: black;
    border-radius: 10%;
    background-color: peru;
}
.container-ans {
  background-color: whitesmoke;
}